* {
  margin: 0;
  padding: 0;
}

body {
  padding-left: 40px;
  padding-right: 40px;
  overflow-x: hidden;
}

.select-container {
  margin-top: 20px;
}

.breed-select {
  width: 150px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 4px;
}

.box {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.cat-info {
  grid-template-columns: 300px 1fr;
  align-items: flex-start;
  gap: 20px;
  display: grid;
}

.loader {
  font-size: 18px;
  font-weight: 600;
}

.is-hidden {
  display: none;
}

.error {
  color: red;
  font-size: 16px;
  font-weight: 500;
}

p {
  text-shadow: 0 2px 2px rgba(255, 255, 255, .4);
}

img {
  box-shadow: 0 19px 38px rgba(0, 0, 0, .3), 0 15px 12px rgba(0, 0, 0, .22);
}

